@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
@import url(https://fonts.googleapis.com/css?family=Amiri:400,400i,700,700i);
@import url(https://fonts.googleapis.com/css2?family=Lateef&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Scheherazade:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
    font-family: Uthmanic;
    src: url(/quran-web/static/media/uthmanic.43269f11.otf) format("opentype");
}

.bismillah {
    display: inline-flex !important;
    text-align: center;
    font-family: Uthmanic;
    margin-bottom: 0.5em;
    line-height: 1.7;
    font-weight: 600;
    justify-content: center;
    width: 100%;
}

.bismillah::after {
    background-image: url(/quran-web/static/media/frame-right.46628c49.png);
    background-size: 30px 50px;
    display: inline-block;
    width: 30px;
    height: 50px;
    content: "";
    margin-left: 10px;
}

.bismillah::before {
    background-image: url(/quran-web/static/media/frame-left.857fe706.png);
    background-size: 30px 50px;
    display: inline-block;
    width: 30px;
    height: 50px;
    content: "";
    margin-right: 10px;
}
